import React, { useState, useEffect } from 'react';
import DropDown from '../forms/DropDown';
import thumbsUp from '../../assets/images/ThumbsUp.png';
import thumbsDown from '../../assets/images/ThumbsDown.png';
//import TimerComponent from '../hooks/TimerComponent';
import getKey from '../utils/KeyGenerator';
import Sounds from '../utils/Sounds.js';
import TimerDisplay from '../utils/TimerDisplay';

const ProjectTaskTracker = () => {

    const [projects, setProjects] = useState(JSON.parse(localStorage.getItem('projects')) || []);
    const [tracking, setTracking] = useState(localStorage.getItem('tracking') || 'tasks');
    const [isCollapsed, setIsCollapsed] = useState(false);
    const [currentTime, setCurrentTime] = useState();
    const [todos, setCurrentTodos] = useState([]);
    const [newProjectDescription, setNewProjectDescription] = useState((tracking === 'sets') ? 'WAVES' : (tracking === 'solar/battery') ? 'Solar / Battery Log' : '');
    //const [newTaskDescription, setNewTaskDescription] = useState('');
    //const [activeProjectIndex, setActiveProjectIndex] = useState(-1);
    const [elapsedTime, setElapsedTime] = useState(0);
    const [clock, setClock] = useState(0);
    const [description, setDescription] = useState();
    const [activated, setActivated] = useState(true);
    const [completed, setCompleted] = useState(false);
    const setTodoCurrentTime = () => {
        console.log(`setTodoCurrentTime => `)
    }
    const setTodos = () => {
        console.log(`setTodos => `)
    }
    const taskInit = () => [{
        description: 'testing 123',
        type: 'timer',
        time: 30000,
        currentTime: 2500,
        activated: activated,
        completed: completed
    }];

    const trackables = ['tasks', 'sets', 'events', 'solar/battery'];

    useEffect(() => {
        const storedProjects = localStorage.getItem('projects');
        if (storedProjects) {
            setProjects(JSON.parse(storedProjects));
        }
    }, []);

    useEffect(() => {
        localStorage.setItem('projects', JSON.stringify(projects));
    }, [projects]);
    /*
        useEffect(() => {
            localStorage.setItem('tracking', tracking);
            console.log(`tracking: ${JSON.stringify(tracking,null,2)}`)
            if (tracking === 'sets') {
                setNewProjectDescription('WAVES');
                return (projects.length<1) ? handleAddProject(): null;
            }
        }, [tracking]);
    */
    useEffect(() => {
        localStorage.setItem('tracking', tracking);
        if (tracking === 'sets') {
            setNewProjectDescription('WAVES');
            const storedProjects = localStorage.getItem('projects');
            if (storedProjects) {
                const currentProjects = JSON.parse(storedProjects);
                if (currentProjects.length < 1) {
                    handleAddProject();
                }
            }
        }
        if (tracking === 'solar/battery') {
            setNewProjectDescription('Solar / Battery Log');
            const storedProjects = localStorage.getItem('projects');
            if (storedProjects) {
                const currentProjects = JSON.parse(storedProjects);
                if (currentProjects.length < 1) {
                    handleAddProject();
                }
            }
        }
    }, [tracking]);

    const toggleTimer = () => {
        console.log(`toggleTimer => timer: ${description} activated: ${activated} completed: ${completed} elapsedTime: ${elapsedTime} - ${(activated) ? '🛑 Stop' : '⏱️ Start'}}`);
        Sounds.playSound(0, 1);
        setActivated(!activated);
    }
    const toggleParentTimer = () => toggleTimer();

    const handleDropdownChange = (event) => {
        setTracking(event.target.value);
    };

    const handleAddProject = () => {
        const project = {
            description: newProjectDescription,
            createdDate: new Date().toLocaleString(),
            startTime: new Date().getTime(),
            tasks: []
        };
        setProjects(prevProjects => [project, ...prevProjects]);
    };

    const addTask = (projectIndex) => {
        const taskDescription = (tracking !== 'solar/battery')
            ? prompt('Enter task description:')
            : null;
        const batteryDescription = (tracking === 'solar/battery')
            ? prompt('Battery Type:')
            : null;
        const batteryLevel = (tracking === 'solar/battery')
            ? prompt('Battery Level:')
            : null;
        const solarDescription = (tracking === 'solar/battery')
            ? prompt('Solar Type:')
            : null;
        const solarLogDescription = `${batteryDescription} + ${solarDescription}: ${batteryLevel}-100%`
        if (taskDescription || batteryDescription) {
            const session = {
                startDate: new Date().toLocaleString(),
                startTime: new Date().getTime(),
                subTasks: [{
                    startDate: new Date().toLocaleString(),
                    startTime: new Date().getTime(),
                    endTime: '',
                    endDate: ''
                }],
                totalTime: '',
                runningTime: '0s'
            }
            const sessions = [];
            sessions.push(session);
            const task = {
                startTime: new Date().getTime(),
                endTime: '',
                description: (tracking === 'tasks') ? taskDescription : solarLogDescription,
                sessions: sessions,
                isRunning: true,
                runningTime: '0s'
            };
            const updatedProjects = [...projects];
            updatedProjects[projectIndex].tasks.unshift(task);
            updatedProjects[projectIndex].isCollapsed = false;
            setProjects(updatedProjects);
        }
    };

    const addSet = (projectIndex) => {
        const updatedProjects = [...projects];
        const taskDescription = `Set ${updatedProjects[projectIndex].tasks.length + 1}`;
        if (taskDescription) {
            const session = {
                startDate: new Date().toLocaleString(),
                startTime: new Date().getTime(),
                subTasks: [{
                    startDate: new Date().toLocaleString(),
                    startTime: new Date().getTime(),
                    endTime: '',
                    endDate: ''
                }],
                endTime: '',
                endDate: '',
                totalTime: '',
                runningTime: '0s'
            }
            const sessions = [];
            sessions.push(session);
            const task = {
                startTime: new Date().getTime(),
                endTime: '',
                description: taskDescription,
                sessions: sessions,
                isRunning: true,
                runningTime: '0s'
            };
            updatedProjects[projectIndex].tasks.unshift(task);
            updatedProjects[projectIndex].isCollapsed = false;
            setProjects(updatedProjects);
        }
    };
    /*
    const handleAddTask = (projectIndex) => {
        const task = {
            description: newTaskDescription,
            startDate: new Date().toLocaleString(),
            startTime: new Date().getTime(),
            endTime: '',
            endDate: '',
            totalTime: '',
            runningTime: '0s',
            isRunning: true
        };
        const updatedProjects = [...projects];
        updatedProjects[activeProjectIndex].tasks.unshift(task);
        setProjects(updatedProjects);
        setNewTaskDescription('');
        setActiveProjectIndex(projectIndex)
    };
    */
    const handleDeleteProject = (index) => {
        const updatedProjects = [...projects];
        updatedProjects.splice(index, 1);
        setProjects(updatedProjects);
    };

    const handleDeleteTask = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        updatedProjects[projectIndex].tasks.splice(taskIndex, 1);
        setProjects(updatedProjects);
    };

    const handleStartTask = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        const task = updatedProjects[projectIndex].tasks[taskIndex];
        task.sessions = task.sessions || [];
        task.isRunning = true;
        //task.startDate = new Date().toLocaleString();
        const session = {
            startDate: new Date().toLocaleString(),
            startTime: new Date().getTime(),
                subTasks: [{
                    startDate: new Date().toLocaleString(),
                    startTime: new Date().getTime(),
                    endTime: '',
                    endDate: ''
                }],
            endTime: ''
        }
        task.sessions.push(session)
        setProjects(updatedProjects);
        startTimer(projectIndex, taskIndex);
    };
    const handleStartSubTask = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        const task = updatedProjects[projectIndex].tasks[taskIndex];
        task.sessions = task.sessions || [];
        task.isRunning = true;
        //task.startDate = new Date().toLocaleString();
        const subtask = {
            startDate: new Date().toLocaleString(),
            startTime: new Date().getTime(),
            endTime: '',
            endDate: ''
        };
        const session = {
            startDate: new Date().toLocaleString(),
            startTime: new Date().getTime(),
            subTasks: [subtask],
            endTime: ''
        }
        task.sessions.push(subtask)
        setProjects(updatedProjects);
        startTimer(projectIndex, taskIndex);
    };
    const addWave = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        const task = updatedProjects[projectIndex].tasks[taskIndex];
        task.sessions = task.sessions || [];
        task.isRunning = false;
        //task.startDate = new Date().toLocaleString();
        const session = {
            startDate: new Date().toLocaleString(),
            startTime: new Date().getTime(),
            subTasks: [{
                startDate: new Date().toLocaleString(),
                startTime: new Date().getTime(),
                endTime: new Date().getTime(),
                endDate: ''
            }],
            endTime: new Date().getTime(),
        }
        task.sessions.push(session)
        setProjects(updatedProjects);
    };
    const subtractWave = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        const task = updatedProjects[projectIndex].tasks[taskIndex];
        task.sessions = task.sessions || [];
        task.isRunning = false;
        task.sessions.pop();
        setProjects(updatedProjects);
    };
    const getTotalTime = (totalTimeMilliseconds) => {
        let hours = Math.floor(totalTimeMilliseconds / 3600000);
        if (hours.toString() !== 'NaN') {
            hours = (hours < 10) ? `0${hours}` : hours;
            let minutes = Math.floor((totalTimeMilliseconds % 3600000) / 60000);
            minutes = (minutes < 10) ? `0${minutes}` : minutes;
            let seconds = Math.floor((totalTimeMilliseconds % 60000) / 1000);
            seconds = (seconds < 10) ? `0${seconds}` : seconds;
            //const totalTime = `${hours}:${minutes}:${seconds}`;
            const totalTime = `${hours}:${minutes}`;
            return totalTime
        }
        return '00:00'
    }
    const handleStopTask = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        const task = updatedProjects[projectIndex].tasks[taskIndex];
        task.sessions = task.sessions || [];
        task.isRunning = false;
        const startTime = task.sessions[task.sessions.length - 1].startTime;
        const startDate = task.sessions[task.sessions.length - 1].startDate;
        const endTime = new Date().getTime();
        const endDate = new Date().toLocaleString();
        const totalTimeMilliseconds = Math.round(endTime - startTime);
        const totalTime = getTotalTime(totalTimeMilliseconds);
        let runningTime = task.sessions[task.sessions.length - 1].runningTime;
        runningTime = (runningTime === undefined) ? task.sessions[task.sessions.length - 2].runningTime : runningTime;
        //alert(`runningTime: ${runningTime}`)
        runningTime = (runningTime === '0s') ? totalTimeMilliseconds : (runningTime + totalTimeMilliseconds);
        const runningTimeDisplay = getTotalTime(runningTime);
        const session = {
            startDate: startDate,
            startTime: startTime,
            subTasks: [{
                startDate: startDate,
                startTime: startTime,
                endDate: endDate,
                endTime: endTime,
            }],
            endDate: endDate,
            endTime: endTime,
            totalTime: totalTime,
            runningTime: runningTime,
            runningTimeDisplay: runningTimeDisplay
        }
        //alert(JSON.stringify(session,null,2))
        task.runningTime = runningTime;
        task.runningTimeDisplay = runningTimeDisplay;
        task.sessions.push(session)
        setProjects(updatedProjects);
        stopTimer(projectIndex, taskIndex);
    };
    const handleStopSubTask = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        const task = updatedProjects[projectIndex].tasks[taskIndex];
        task.sessions = task.sessions || [];
        task.isRunning = false;
        const startTime = task.sessions[task.sessions.length - 1].startTime;
        const startDate = task.sessions[task.sessions.length - 1].startDate;
        const endTime = new Date().getTime();
        const endDate = new Date().toLocaleString();
        const totalTimeMilliseconds = Math.round(endTime - startTime);
        const totalTime = getTotalTime(totalTimeMilliseconds);
        let runningTime = task.sessions[task.sessions.length - 1].runningTime;
        runningTime = (runningTime === undefined) ? task.sessions[task.sessions.length - 2].runningTime : runningTime;
        //alert(`runningTime: ${runningTime}`)
        runningTime = (runningTime === '0s') ? totalTimeMilliseconds : (runningTime + totalTimeMilliseconds);
        const runningTimeDisplay = getTotalTime(runningTime);
        const session = {
            startDate: startDate,
            startTime: startTime,
            subTasks: [{
                startDate: startDate,
                startTime: startTime,
                endDate: endDate,
                endTime: endTime,
            }],
            endDate: endDate,
            endTime: endTime,
            totalTime: totalTime,
            runningTime: runningTime,
            runningTimeDisplay: runningTimeDisplay
        }
        //alert(JSON.stringify(session,null,2))
        task.runningTime = runningTime;
        task.runningTimeDisplay = runningTimeDisplay;
        task.sessions.push(session)
        setProjects(updatedProjects);
        stopTimer(projectIndex, taskIndex);
    };
    const startTimer = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        const task = updatedProjects[projectIndex].tasks[taskIndex];

        task.timerId = setInterval(() => {
            setProjects(updatedProjects);
        }, 1000);
    };

    const stopTimer = (projectIndex, taskIndex) => {
        const updatedProjects = [...projects];
        const task = updatedProjects[projectIndex].tasks[taskIndex];
        const session = task.sessions[task.sessions.length - 1]
        clearInterval(task.timerId);
        task.sessions.splice((task.sessions.length - 2), 1);
        task.totalTime = task.totalTime + session.runningTime;
        setProjects(updatedProjects);
    };

    const handleToggleCollapse = () => {
        setIsCollapsed(!isCollapsed);
    };
    const handleProjectToggle = (projectIndex) => {
        //setIsCollapsed(!isCollapsed);
        const updatedProjects = [...projects];
        const project = updatedProjects[projectIndex];
        project.isCollapsed = !project.isCollapsed;

        setProjects(updatedProjects);
        return (!project.isCollapsed) ? setIsCollapsed(false) : null;
    };

    const getProjectTime = (project) => {
        let projectTotal = 0;
        project.tasks.forEach((task) => {
            console.log(`${project.description} task: ${getTotalTime(task.runningTime)}`)
            projectTotal += task.runningTime;
        });
        project.totalTime = projectTotal;
        console.log(`getProjectTime => ${project.description}: ${getTotalTime(projectTotal)}`)
        return projectTotal;
    };
    const capitalizeString = (str) => str.charAt(0).toUpperCase() + str.slice(1);
    const getCategory = (category) => {
        let label = capitalizeString(category);
        if (label.endsWith('s')) {
            label = capitalizeString(label).slice(0, -1);
        }
        return label;
    }

    return <div className='mt--30 bg-lite'>
            <div className='containerBox'>
                <div className='flexContainer containerBox'>
                    <div className='flex2Column columnRightAlign size25 r-5 bold color-soft'>
                        Tracking:
                    </div>
                    <div className='flex2Column columnLeftAlign'>
                        <DropDown
                            value={tracking}
                            options={trackables}
                            onChange={handleDropdownChange}
                            classes='color-lite size20 bg-dark'
                        />
                    </div>
                </div>
                <div className='containerBox'>
                    <input
                        className='size25 bold color-lite bg-dark width-100-percent'
                        type="text"
                        value={newProjectDescription}
                        onChange={(e) => setNewProjectDescription(e.target.value)}
                        onKeyDown={(e) => {
                            if (e.key === 'Enter') {
                                e.preventDefault();
                                handleAddProject();
                            }
                        }}
                    />
                    <div className='containerBox bold bg-lite color-light size25 button mt-20 mb-20' onClick={handleAddProject}>
                        Add Log
                    </div>
                </div>
            </div>
            <div className='containerBox'>
                <div className='containerBox size25 color-lite bg-lite bold button' onClick={handleToggleCollapse}>
                    {isCollapsed ? 'Expand All' : 'Collapse All'}
                </div>
            </div>
        {(tracking === 'tasks') ?
            <div>
                {projects.map((project, projectIndex) => (
                    <div key={projectIndex} className='containerBox'>
                        <div className='containerBox'>
                            <div className='containerBox bold color-neogreen m-10 size30 lh-30'>
                                {project.description}
                            </div>
                            <div className='containerBox contentCentered'>
                                <div className='containerBox color-lite size25 bold m-5 contentCentered'>
                                    {String(project.createdDate).split(', ')[0]} - {String(project.createdDate).split(', ')[1].split(':')[0]}:{String(project.createdDate).split(', ')[1].split(':')[1]}
                                </div>
                                <div className=' color-lite size20 m-10 contentCentered'>
                                    ⏱️ Start {getTotalTime(getProjectTime(project))}
                                </div>
                            </div>
                            <div className='containerBox flexContainer'>
                                <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => addTask(projectIndex)}>
                                    + ⏱️ Start
                                </div>
                                <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => handleDeleteProject(projectIndex)}>
                                    🗑️ Delete
                                </div>
                                <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => handleProjectToggle(projectIndex)}>
                                    {project.isCollapsed ? 'Expand' : 'Collapse'}
                                </div>
                            </div>
                        </div>
                        <div className='color-lite size15 bold containerBox'>{(isCollapsed || project.isCollapsed) ? '' : `${project.description} Tasks:`}</div>
                        <div>
                            {(isCollapsed || project.isCollapsed) ? null : project.tasks.map((task, taskIndex) => (
                                <div key={taskIndex} className='containerBox lowerBorder'>
                                    <div className='containerBox'>
                                        <div className='bold color-yellow size25 lh-30'>{task.description}</div>
                                        <div>{task.sessions.length} Total: {task.runningTimeDisplay} </div>
                                    </div>
                                        {task.sessions.map((session, sessionIndex) => (
                                            <div className='color-lite containerBox' key={getKey('tracker')}>
                                                {task.sessions.map((session, sessionIndex) => (
                                                    <div className='containerBox flexContainer'>
                                                        <div className='color-yellow bold flex1Auto mt-10 mb-10'>
                                                            {String(session.startDate).split(', ')[0]}
                                                        </div>
                                                        <div className='columnLeftAlign flex2Column mt-10 mb-10'>
                                                            {String(session.startDate).split(', ')[1]} - {String(session.endDate).split(', ')[1]} 
                                                        </div>
                                                    </div>
                                                ))}
                                                <div className='width-100-percent'>
                                                   <TimerDisplay 
                                                        totalTime={session.totalTime} 
                                                        toggleParentTimer={toggleParentTimer} 
                                                        projectIndex={projectIndex} 
                                                        taskIndex={taskIndex} 
                                                        handleStopSubTask={handleStopSubTask}
                                                        handleStartSubTask={handleStartSubTask}
                                                    />
                                                    {/*
                                                    <TimerComponent
                                                        todo={todo}
                                                        index={index}
                                                        setTodoCurrentTime={setTodoCurrentTime}
                                                        setTodos={setTodos}
                                                        todos={todos}
                                                        toggleCheckbox={toggleCheckbox}
                                                        onReset={onReset}
                                                    />*/}
                                                </div>
                                            </div>
                                        ))}
                                    <div className='containerBox flexContainer'>
                                    {task.isRunning ? (
                                        <div className='containerBox flex2Column bg-lite button m-5' onClick={() => handleStopTask(projectIndex, taskIndex)}>
                                            🛑 Stop 
                                        </div>
                                    ) : (
                                        <div className='containerBox flex2Column bg-lite button m-5' onClick={() => handleStartTask(projectIndex, taskIndex)}>
                                            ⏱️ Start
                                        </div>
                                    )}
                                        <div className='containerBox flex2Column bg-lite button m-5' onClick={() => handleDeleteTask(projectIndex, taskIndex)}>
                                            🗑️ Delete
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                ))}
            </div>
            : <React.Fragment></React.Fragment>}

        {(tracking === 'solar/battery') ?
            <div>
                <div className='containerBox'>
                    <div className='containerBox bold size30 color-yellow'>Solar/Battery Log</div>
                    <input
                        className='containerBox width-100-percent'
                        type="text"
                        value={newProjectDescription}
                        onChange={(e) => setNewProjectDescription(e.target.value)}
                        onKeyDown={(e) => {
                            if (e.key === 'Enter') {
                                e.preventDefault();
                                handleAddProject();
                            }
                        }}
                    />

                    <div className='containerBox bg-lite button' onClick={handleAddProject}>
                        Add Project
                    </div>
                </div>
                <br />
                <div>
                    {projects.map((project, projectIndex) => (
                        <div key={projectIndex} className='containerBox'>
                            <div className='containerBox'>
                                <div className='containerBox size30 mb-10 bold color-neogreen lh-30'>
                                    {project.description}
                                </div>
                                <div className='containerBox'>
                                    <div className='color-lite size25 bold m-5'>
                                        {String(project.createdDate).split(', ')[0]}
                                    </div>
                                    <div className='color-lite size25 m-5'>
                                        {String(project.createdDate).split(', ')[1]}
                                    </div>
                                    <div className='color-lite size20 m-10'>
                                        ⏱️ Start {getTotalTime(getProjectTime(project))}
                                    </div>
                                </div>
                                <div className='containerBox flexContainer'>
                                    <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => addTask(projectIndex)}>
                                        + ⏱️ Start
                                    </div>
                                    <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => handleDeleteProject(projectIndex)}>
                                        🗑️ Delete
                                    </div>
                                    <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => handleProjectToggle(projectIndex)}>
                                        {project.isCollapsed ? 'Expand' : 'Collapse'}
                                    </div>
                                </div>
                            </div>
                            <div>
                                {(isCollapsed || project.isCollapsed) ? null : project.tasks.map((task, taskIndex) => (
                                    <div key={taskIndex} className='containerBox lowerBorder'>
                                        <div className='containerBox'>
                                            <div className='bold color-yellow size25 lh-30'>{task.description}</div>
                                            <div className='containerBox bold color-lite size25'>
                                                <div className='mb-20'>{task.sessions.length} Total</div>
                                                <div className='containerBox bg-veryLite'>{task.runningTimeDisplay}</div>
                                            </div>
                                        </div>
                                        <div className='containerBox'>
                                            {task.sessions.map((session, sessionIndex) => (
                                                <div className='color-lite'>
                                                    <div className='containerBox'>
                                                        <div className='columnLeftAlign size25 mb-5'>
                                                            <span className='bold mr-10 color-yellow size25 lh-30'>
                                                                {sessionIndex+1}.
                                                            </span> 
                                                            {String(session.startDate).split(', ')[0]} 
                                                        </div>
                                                        <div className='ml-30 columnLeftAlign size15'>
                                                            {String(session.startDate).split(', ')[1]} - {String(session.endDate).split(', ')[1]}
                                                        </div>
                                                        <div className='ml-30 columnLeftAlign size20 bold'>
                                                            {session.totalTime}
                                                        </div>
                                                    </div>
                                                </div>
                                            ))}
                                        </div>
                                        <div className='containerBox flexContainer'>
                                            {task.isRunning ? (
                                                <div className='containerBox flex2Column m-5 bg-lite button' onClick={() => handleStopTask(projectIndex, taskIndex)}>
                                                    🛑 Stop
                                                </div>
                                            ) : (
                                                <div className='containerBox flex2Column m-5  bg-lite button' onClick={() => handleStartTask(projectIndex, taskIndex)}>
                                                    ⏱️ Start
                                                </div>
                                            )}
                                            <div className='containerBox flex2Column m-5  bg-lite button' onClick={() => handleDeleteTask(projectIndex, taskIndex)}>
                                                🗑️ Delete
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
            : <React.Fragment></React.Fragment>
        }

        {(tracking === 'sets') ?
            <React.Fragment>
                <div className='containerBox bold size30'>
                    Track Sets
                </div>
                <div>
                    {projects.map((project, projectIndex) => (
                        <div key={projectIndex} className='containerBox'>
                            <div className='containerBox'>
                                <div className='containerBox bold color-neogreen size30 lh-30'>{project.description}</div>
                                    <div className='containerBox'>   
                                        <div className='size20 color-lite bold m-5'>
                                            {String(project.createdDate).split(', ')[0]}
                                        </div>
                                        <div className='size20 color-lite m-5'>
                                            {String(project.createdDate).split(', ')[1]}
                                        </div>
                                        <div className='color-lite size20 m-10'>
                                            ⏱️ Start {getTotalTime(getProjectTime(project))}
                                        </div>
                                    </div>
                                <div className='containerBox flexContainer'>
                                    <div className='containerBox flex2Column m-10 bg-lite button' onClick={() => addSet(projectIndex)}>
                                        + 🌊🌊🌊
                                    </div>
                                    <div className='containerBox flex2Column m-10 bg-lite button' onClick={() => handleDeleteProject(projectIndex)}>
                                        🗑️ Delete
                                    </div>
                                </div>
                            </div>
                            <div>
                                {(isCollapsed || project.isCollapsed) ? null : project.tasks.map((task, taskIndex) => (
                                    <div key={taskIndex} className='containerBox lowerBorder'>
                                        <div className='containerBox bold'>
                                            <div className='color-yellow size25 lh-30'>{task.description}</div>
                                            <div>Waves: {task.sessions.length}</div>
                                        </div>
                                        <div className='containerBox flexContainer'>
                                            <div className='containerBox flex3Column bg-lite button' onClick={() => addWave(projectIndex, taskIndex)}>
                                                + 🌊 
                                            </div>
                                            <div className='containerBox flex3Column bg-lite button' onClick={() => subtractWave(projectIndex, taskIndex)}>
                                                - 🌊
                                            </div>
                                            <div className='containerBox flex3Column bg-lite button' onClick={() => handleDeleteTask(projectIndex, taskIndex)}>
                                                🗑️ Delete
                                            </div>
                                        </div>
                                        <div className='containerBox'>
                                            {task.sessions.map((session, sessionIndex) => (
                                                <div className='containerBox color-lite bg-lite'>
                                                    <div className='button' onClick={() => task.sessions[sessionIndex].isLiked = !task.sessions[sessionIndex].isLiked}>
                                                        <span className='mr-5 size15'><span className='bold'>Wave {sessionIndex + 1}</span> - {String(session.startDate).split(', ')[1]}</span>
                                                        <img src={(task.sessions[sessionIndex].isLiked) ? thumbsUp : thumbsDown} alt='like' className='shaka mb--10' />
                                                    </div>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            </React.Fragment>
            : <React.Fragment></React.Fragment>}
    </div>
};

export default ProjectTaskTracker;
