import React, { useState, useEffect } from 'react';
import getKey from '../utils/KeyGenerator.js';
import { newDate } from '../utils/Dates.js';
import { losePriority } from './SurfPriority.js';
import getAthleteScore from './GetAthleteScore.js';
import getPriorityColor from './GetPriorityColor.js';
import Sounds from '../utils/Sounds.js';

const SurfScores = ({ players, setPlayers, heatLength, setStartTime, setSurfScore}) => {
    
    console.log(`SurfScores => heatLength: ${heatLength}`)
    console.log(`SurfScores => surfWinner: ${localStorage.getItem('surfWinner')}`)
    const [timerOn, setTimerOn] = useState(false);
    const [timesUp, setTimesUp] = useState(false);
    const [time, setTime] = useState((Number(localStorage.getItem('surfWinner')) || heatLength) * 60); // Initial time in seconds (e.g., 1 hour)
    const [playerIdScored, setPlayerIdScored] = useState();
    const [scoreIdScored, setScoreIdScored] = useState();

    useEffect(() => {
        let intervalId;
        if (timerOn) {
            intervalId = setInterval(() => {
                setTime((prevTime) => {
                    const newTime = prevTime - 1;
                    if (newTime <= 10 && newTime > 0 ) {
                        Sounds.playSound(0, newTime);
                    }
                    if (newTime <= 0) {
                        Sounds.playSound(0, newTime);
                        clearInterval(intervalId);
                        setTimesUp(true);
                        return 0;
                    }
                    return newTime;
                });
            }, 1000);
        }

        return () => clearInterval(intervalId);
    }, [timerOn]);

    useEffect(() => {
        console.log(`SurfScores => time: ${time}`)
        if (time === 0) {
            //getAthleteScore();
        }
    }, [time]);

    useEffect(() => {
        if (timesUp) {
            setTimerOn(false);
            //setTime(heatLength);
            console.log(`heatLength: ${heatLength}`)
            setTimesUp(true);
        }
    }, [timesUp]);

    const toggleTimer = () => {
        if (timerOn) {
            setTimerOn(false)
        } else {
            console.log(`time: ${time}`)
            if (time === 0) {
                setTime(heatLength*60);
            }
            setTimerOn(true)
            setStartTime();
        }
    }
    const restartTimer = () => {
        setTime(heatLength*60);
        setTimerOn(true);
    }

    //const surfClass = (playerIndex) => 'flex3Column p-20 size25 bold' + ((isWinner(playerIndex)) ? ' color-neogreen shakingShaka mt-20' : '');
    const getTimerButtonClasses = () => {
        const timerButtonClasses = timerOn ? 'bg-lite' : 'bg-tinted';
        return timerButtonClasses;
    }
    const getTimerClasses = () => {
        const timerClasses = (time < 120) ? 'blinking-fade' : '';
        return timerClasses;
    }
    const formatTime = (timeInSeconds) => {
        const hours = Math.floor(timeInSeconds / 3600);
        const minutes = Math.floor((timeInSeconds % 3600) / 60);
        const seconds = timeInSeconds % 60;
        const formattedHours = hours.toString().padStart(2, '0');
        const formattedMinutes = minutes.toString().padStart(2, '0');
        const formattedSeconds = seconds.toString().padStart(2, '0');
        return `${formattedHours}:${formattedMinutes}:${formattedSeconds}`;
    };

    const getPriorityItem = (index) => {
            return <div className={`button flex${players.length}Column r-10 m-5 w-75 size40 pt-20 pb-20 bg-${getPriorityColor(players, index)}`} key={getKey(`priorityFlag`)} onClick={() => losePriority(players, setPlayers, index)}>
                        {index}
                    </div>
    }
       
    const getPriorityDisplay = () => {
        return <div className='containerBox'>
                    <div className='containerBox'>Priority</div>
                    <div className='flexContainer mt-5 color-dark text-outline-light' >
                        {players.map((player, index) => getPriorityItem(index+1))}
                    </div>
                </div>
    }
    const setNewScore = (playerId, scoreId) => {
        const newScore = prompt(`Enter ${players[playerId].player || players[playerId].name}'s score for wave ${scoreId + 1}: `, '');
        setSurfScore(playerId, scoreId, newScore);
        setPlayerIdScored(playerId);
        setScoreIdScored(scoreId);
    }
    const addNewScore = (playerId) => {
            const emptyScore = players[playerId].surfScores.indexOf('');
            const zeroScore = players[playerId].surfScores.indexOf(0);
            const isScoringComplete = (zeroScore === -1 && emptyScore === -1) ? true : false;
            if (isScoringComplete) {
                alert(`${players[playerId].name} has ${players[playerId].surfScores.length} waves. Selet the wave/score you wish to edit in the scorecard below.`)
                return
            }
            const scoreId = (zeroScore !== -1 && zeroScore < emptyScore) ? zeroScore : emptyScore;
            const isPlayerRepeat = (playerIdScored === playerId) ? true : false;
            const isScoreRepeat = (scoreIdScored === scoreId) ? true : false;
            //alert(`${players[playerId].name} scoreId: ${scoreId} empty score: ${emptyScore} zero score: ${zeroScore} scores: ${JSON.stringify(players[playerId].surfScores,null,2)}`)
            //if (isPlayerRepeat) {
                //if (!isScoreRepeat) {
                    setNewScore(playerId, scoreId);
                //}
            //} else {
                //setNewScore(playerId, scoreId);
            //}
    }
        
    const getScoreDisplay = () => {
        return <div className='containerBox'>
            <div className='bold '>
                <div className='containerBox color-lite'>Scoreboard - {newDate()}</div>
                <div className='flexContainer'>
                    {players.map((player, index) => <div className={` button flex${players.length}Column`} onClick={() => addNewScore(index)}>
                                {getAthleteScore(players, index)}
                            </div>
                    )}
                </div>
            </div>
        </div>
    }

    return <React.Fragment>
        {getScoreDisplay()}
        {getPriorityDisplay()}
        <div className='containerBox flexContainer size20 bold'>
            <div className={`flex3Column r-10 pt-10 pb-10 button color-lite ${getTimerButtonClasses()}`} onClick={() => toggleTimer()}>
                {(timerOn) ? 'STOP' : 'START'}
            </div>
            <div className='ml-5 mr-5 flex3Column size25 r-10 bg-tinted pt-10 pb-10 color-lite'>
                <span className={getTimerClasses()}>
                    {formatTime(time)}
                </span>
            </div>
            <div className={`flex3Column r-10 pt-10 pb-10 color-lite button ${getTimerButtonClasses()}`} onClick={() => restartTimer()}>
                RESTART
            </div>
        </div>
    </React.Fragment>

}

export default SurfScores;